<template>
  <v-container>
    
    <!-- *********** Header ************** -->
    <section>
      <v-app-bar
          class="app-bar"
          elevation="4"
          color="#FABFAD"
      >
      <img
        height="30"
        src="../assets/rio-mare-logo.png"
      >
     
      <v-spacer></v-spacer>

   <router-link to="/">
      <v-btn icon style="color: #01184d">
        <v-icon>mdi-home</v-icon>
      </v-btn>
    </router-link>
      </v-app-bar>
    </section>

    <!-- *************  Body    *************** -->
    <section v-if="loaded">
    <section class="pr-5 pl-5 mt-15">
      <p>
        Di seguito trovi il risultato della lettura da parte del nostro servizio intelligente.
      </p>
      <p class="mt-2">
        Confidenza dell'elaborazione:
      </p>
      <h1 class="text-center" :class="{'text-success': confTot >= 90, 'text-danger': confTot < 90 && confTot >= 70, 'text-error': confTot < 70 }" style="font-size: 28px">
        <b>
          {{ confTot }}%
        </b>
      </h1>
      <p>
        Ti chiediamo di confermare la lettura fatta, e modificarla se necessario.
      </p>
    </section>

    <section class="pl-5 pr-5">
      <v-row>
        <v-col cols="8">
          <p><b>Dati Rilevati </b></p>
        </v-col>
        <v-col cols="4">
          <b>Confidenza</b>
        </v-col>
      </v-row>
      <v-row class="pl-3">
        <v-col cols="8" class="pa-0">
          <v-text-field
            label="Codice Lotto"
            placeholder="Inserisci codice lotto"
            outlined
            :value="codLotto"
          ></v-text-field>
        </v-col>
        <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': codlottoConf >= 90, 'text-danger': codlottoConf < 90 && codlottoConf >= 70, 'text-error': codlottoConf < 70 }">
          <h2><b>{{ codlottoConf }}%</b></h2>
        </v-col>
      </v-row>
      <v-row class="pl-3">
        <v-col cols="8" class="pa-0">
          <v-text-field
            label="Linea Produzione"
            placeholder="Inserisci linea produzione"
            :value="lineaProd"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': lineaProdConf >= 90, 'text-danger': lineaProdConf < 90 && lineaProdConf >= 70, 'text-error': lineaProdConf < 70 }">
          <h2><b> {{ lineaProdConf }}%</b></h2>
        </v-col>
      </v-row>
          <v-row class="pl-3">
            <v-col cols="8" class="pa-0">
              <v-text-field
            label="Ora Produzione"
            placeholder="Inserisci ora produzione"
            :value="oraProd"
            outlined
          ></v-text-field>
            </v-col>
            <v-col cols="4" class="pa-0 d-flex justify-center mt-2" :class="{'text-success': oraProdConf >= 90, 'text-danger': oraProdConf < 90 && oraProdConf >= 70, 'text-error': oraProdConf < 70 }">
              <h2><b>{{ oraProdConf }}%</b></h2>
            </v-col>
          </v-row>
    </section>

    <!--  ******************   Button Footer    ********************* -->
    <section style="position: fixed; bottom: 30px; width: 100%; left: 0; text-align: center" class="pl-5 pr-5 pt-5">
      <v-row>
      <v-col cols="12">
      <router-link to="/customize_picture">
        <v-btn
          block
          color="primary"
          elevation="5"
          large
        >
        Scopri la tracciabilit
        </v-btn>
      </router-link>
      </v-col>
    </v-row>
    </section>
    </section>
    <section v-else style="height: 75vh" class="d-flex justify-center align-center">
      <v-progress-circular
      :size="70"
      :width="7"
      color="primary"
      indeterminate
    ></v-progress-circular>
    </section>
    
  </v-container>
</template>

<script>
import axios from 'axios'
  export default {
    name: 'TraceabilityComponent',
    data () {
      return {
        file: null,
        loaded: false,
        codiceLottoConfidenza: 0,
        codiceLotto: '',
        lineaProduzioneConfidenza: 0,
        lineaProduzione: '',
        oraProduzioneConfidenza: 0,
        oraProduzione: ''
      }
    },
    async mounted () {
      let fileBase64 = sessionStorage.getItem('code')
        var ImageURL = fileBase64
        var block = ImageURL.split(";");
        var realData = block[1].split(",")[1];

        var blob = this.b64toBlob(realData, "image/png");



      let formData = new FormData();
      formData.append("Image", blob, "code.png");
      formData.append("TipoLotto", "l");
      axios.post('https://boltonocr.azurewebsites.net/elabora-foto', formData, {
        headers: {
            'Content-Type': 'multipart/form-data'
        } 
    })
      .then((res) => {
        console.log(res)
        let result = res.data
        this.codiceLottoConfidenza = result.codiceLottoConfidenza,
        this.codiceLotto = result.codiceLotto,
        this.lineaProduzioneConfidenza = result.lineaProduzioneConfidenza,
        this.lineaProduzione = result.lineaProduzione,
        this.oraProduzioneConfidenza = result.oraProduzioneConfidenza,
        this.oraProduzione = result.oraProduzione
        this.loaded = true
      })
      .catch((error) => {
        console.log(error)
        this.loaded = true
      })
    },
    computed: {
      codLotto () {
        return this.codiceLotto
      },
      codlottoConf () {
        return Math.trunc((this.codiceLottoConfidenza * 100))
      },
      lineaProd () {
        return this.lineaProduzione
      },
      lineaProdConf () {
        return Math.trunc(this.lineaProduzioneConfidenza * 100)
      },
      oraProd () {
        return this.oraProduzione
      },
      oraProdConf () {
        return Math.trunc(this.oraProduzioneConfidenza * 100)
      },
      confTot () {
        let sum = (this.lineaProduzioneConfidenza + this.codiceLottoConfidenza + this.oraProduzioneConfidenza) * 100
        if (sum > 0) {
          return Math.round((sum / 3))
        } else {
          return 0
        }
      }
    },
    methods: {
      async dataUrlToFile (dataUrl, fileName, mimeType) {
        const res = await axios(dataUrl);
        const blob = res.data;
        return new File([blob], fileName, { type: mimeType });
      },

      b64toBlob(b64Data, contentType, sliceSize) {
        contentType = contentType || '';
        sliceSize = sliceSize || 512;

        var byteCharacters = atob(b64Data);
        var byteArrays = [];

        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
            var slice = byteCharacters.slice(offset, offset + sliceSize);

            var byteNumbers = new Array(slice.length);
            for (var i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
            }

            var byteArray = new Uint8Array(byteNumbers);

            byteArrays.push(byteArray);
        }

      var blob = new Blob(byteArrays, {type: contentType});
      return blob;
}
    }

    
  }
</script>
